    <v-row cols="12">
      <v-col cols="12">
        <!-------------->
        <!-- About me -->
        <!-------------->
        <v-container class="d-block pt-0" data-aos="slide-right" data-aos-delay="100">
          <v-card
            min-height="300px"
            min-width="60%"
            :width="$vuetify.breakpoint.lgAndUp ? '75%' : '100%'"
            color="transparent"
            flat
          >
            <p class="content-title primary--text">
              About me
              <v-icon x-large color="white" class="pl-3 pb-1">mdi-card-account-details</v-icon>
            </p>
            <p
              class="content-text primary--text"
            >I'm a student based in Florence (Italy), currently studying IT engineering at the University of Florence.</p>
            <div class="content-text primary--text pb-3">
              I studied informatics at the
              <a
                href="https://www.itismeucci.edu.it/"
                target="_blank"
                rel="noopener noreferrer"
              >ITIS Meucci</a>
              school (Florence).
              <br />Here I learned many technical skills, such as:
              <ul>
                <li>Programming (Assembly, C, C++, Java, Python, HTML, CSS, JavaScript, PHP, SQL, Vue.js)</li>
                <li>Networking and distribuited applications</li>
                <li>Telecommunications and basic electronics</li>
                <li>Project and business management</li>
                <li>Cybersecurity</li>
              </ul>
            </div>
          </v-card>
        </v-container>
      </v-col>
      <v-col cols="12">
        <!-------------->
        <!-- Projects -->
        <!-------------->
        <v-container class="d-block pt-0" data-aos="slide-left" data-aos-delay="200">
          <v-card
            min-height="300px"
            min-width="60%"
            :width="$vuetify.breakpoint.lgAndUp ? '75%' : '100%'"
            max-width="100%"
            color="transparent"
            flat
            class="float-right text-right"
          >
            <p class="content-title primary--text">
              <v-icon x-large color="white" class="pr-6 pb-1">mdi-briefcase</v-icon>Projects
            </p>
            <p class="content-text primary--text">
              The first real-life project I was part of is ScuolaFacile, a web platform that lets schools easily manage timetables and substitute teachers. You can check it out
              <a
                href="https://scuolafacile.itismeucci.it/"
