                rel="noopener noreferrer"
              >here</a>.
            </p>
            <p class="content-text primary--text">
              For more projects, check out my
              <a
                href="https://github.com/RonPlusSign"
                target="_blank"
                rel="noopener noreferrer"
              >GitHub profile</a>!
