          </v-card>
        </v-container>
      </v-col>
      <v-col cols="12">
        <!----------------->
        <!-- Photography -->
        <!----------------->
        <v-container class="d-block pt-0" data-aos="slide-right" data-aos-delay="300">
          <v-card
            min-height="300px"
            :width="$vuetify.breakpoint.lgAndUp ? '75%' : '100%'"
            color="transparent"
            flat
          >
            <p class="content-title primary--text">
              Photography
              <v-icon x-large color="white" class="pl-1 pb-1">mdi-camera-iris</v-icon>
            </p>
            <p
              class="content-text primary--text"
            >In my free time love travelling and take some shots around me using my Canon 700D.</p>
            <p class="content-text primary--text">
              If you want to check some works of mine, take a look at
              <router-link to="/gallery">my gallery</router-link>
              {{" "}}or check out my
              <a
                href="https://www.instagram.com/andreadelli_/"
                target="_blank"
                rel="noopener noreferrer"
              >Instagram account</a>!
            </p>
          </v-card>
        </v-container>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import HomeTitle from "@/components/HomeTitle.vue";
import ScrollForMoreBtn from "@/components/ScrollForMoreBtn.vue";

export default {
  name: "Home",
  components: { HomeTitle, ScrollForMoreBtn },

  data() {
    return {
      minPortraitOpacity: 0,
      defaultPortraitOpacity: 0.8,
    };
  },

  created() {
    this.$vuetify.theme.dark = true;

    window.addEventListener("scroll", this.handleScroll);
  },

  mounted() {
    document.querySelector(
      "#portrait"
    ).style.opacity = this.defaultPortraitOpacity;
  },

  methods: {
    handleScroll() {
      let scrollPercentage = window.scrollY / window.innerHeight;

      let opacity = this.defaultPortraitOpacity - scrollPercentage;

      if (opacity < this.minPortraitOpacity) opacity = this.minPortraitOpacity;

      try {
        document.querySelector("#portrait").style.opacity = opacity;
      } catch (error) {
        error; // To avoid to log an error if there's no #portrait in the current page
      }
    },
  },
};
</script>

<style scoped>
.home {
  overflow: hidden;
}

#portrait {
  position: absolute;
  top: 0;
}

/* Small devices (768px and up) (768px and down) */
@media only screen and (max-width: 768px) {
  .content-text {
    font-weight: 100 !important;
    font-size: 1.2rem;
  }
}

/* Medium and large devices (768px and up) */
@media only screen and (min-width: 768px) {
  .content-text {
    font-weight: 100 !important;
    font-size: 1.7rem;
  }
}

.content-title {
  font-size: 2.5rem;
  font-weight: 900;
}

a {
  color: white !important;
}
</style>