<template>
  <v-btn
    v-show="isVisible"
    @click="scroll"
    id="scrollBtn"
    class="mb-0 pt-2"
    fixed
    bottom
    dark
    text
    height="auto"
    :width="$vuetify.breakpoint.xsOnly ? '100%' : 'auto'"
  >
    <v-row cols="12">
      <v-col cols="12" class="pa-0">
        <p class="ma-0">Scroll for more</p>
      </v-col>
      <v-col class="pa-0">
        <svg id="more-arrows">
          <polygon class="arrow-top" points="37.6,27.9 1.8,1.3 3.3,0 37.6,25.3 71.9,0 73.7,1.3 " />
          <polygon
            class="arrow-middle"
            points="37.6,45.8 0.8,18.7 4.4,16.4 37.6,41.2 71.2,16.4 74.5,18.7 "
          />
          <polygon
            class="arrow-bottom"
            points="37.6,64 0,36.1 5.1,32.8 37.6,56.8 70.4,32.8 75.5,36.1 "
          />
        </svg>
      </v-col>
    </v-row>
  </v-btn>
</template>

<script>
export default {
  name: "ScrollForMoreBtn",

  data() {
    return {
      isVisible: true,
    };
  },

  created() {
    window.addEventListener("scroll", this.isScrollBtnVisible);
  },
  

  methods: {
    scroll: function () {
      window.scrollBy(0, window.screen.height * 0.8);
    },

    isScrollBtnVisible: function () {
      let scrollPercentage = window.scrollY / window.innerHeight;

      this.isVisible = scrollPercentage < 0.3;
    },
  },
};
</script>

<style lang="scss" scoped>
#scrollBtn {
  &:hover {
    polygon {
      fill: #fff;
      transition: all 0.2s ease-out;

      &.arrow-bottom {
        transform: translateY(-18px);
      }

      &.arrow-top {
        transform: translateY(18px);
      }
    }
  }
}

/* Arrow & Hover Animation */
#more-arrows {
  width: 75px;
  transform: scale(0.4);
  height: 65px;
}

polygon {
  fill: #fff;
  transition: all 0.2s ease-out;

  &.arrow-middle {
    opacity: 0.75;
  }

  &.arrow-top {
    opacity: 0.5;
  }
}
</style>