import Vue from 'vue'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'

Vue.use(Vuetify)

export default new Vuetify({
    theme: {
        options: {
            customProperties: true,
        },
        themes: {
            light: {
                primary: "#000",
                secondary: "#FFF",
            },
            dark: {

                primary: "#FFF",
                secondary: "#123" //original: #193650
            },
        },
    },
})