<template>
  <v-app-bar
    app
    :flat="!hasScrolled"
    elevate-on-scroll
    :color="hasScrolled ? 'secondary' : 'transparent'"
    id="appbar"
  >
    <router-link to="/" tag="a">
      <v-row class="pl-3">
        <img v-if="$vuetify.theme.dark" src="@/static/logo-white.svg" id="nav-logo" />
        <img v-else src="@/static/logo-black.svg" id="nav-logo" />
        <span class="text-decoration-none pl-3" id="nav-name">Andrea Delli</span>
      </v-row>
    </router-link>
    <v-spacer />
    <!---------------->
    <!-- Menu pages -->
    <!---------------->
    <div class="d-none d-md-flex" justify="end">
      <!-- Photography -->
      <router-link to="/gallery" class="link px-3">Photography</router-link>
      <!-- GitHub -->
      <a
        href="https://github.com/RonPlusSign"
        target="_blank"
        rel="noopener noreferrer"
        class="link px-3"
      >GitHub</a>

      <!-- Curriculum Vitae -->
      <!-- :href="require('@/static/Delli Andrea - Curriculum Vitae.pdf')" -->
      <a
        href="https://europa.eu/!GG64Dy"
        target="_blank"
        rel="noopener noreferrer"
        class="link px-3"
      >Curriculum Vitae</a>
      <!-- Contact me -->
      <router-link to="/contact" class="link px-3">Contact me</router-link>
    </div>

    <!------------------------>
    <!-- Small devices menu -->
    <!------------------------>
    <DialogMenu />
  </v-app-bar>
</template>

<script>
import DialogMenu from "@/components/DialogMenu.vue";

export default {
  name: "AppBar",
  components: { DialogMenu },

  data() {
    return {
      dialog: false,
      hasScrolled: false,
    };
  },

  methods: {
    handleScroll() {
      this.hasScrolled = window.scrollY / window.innerHeight > 0.2;
    },
  },

  created() {
    window.addEventListener("scroll", this.handleScroll);
  },
};
</script>

<style scoped>
.link {
  font-size: 1.1rem;
  font-weight: 600;
}

/* Small devices (768px and up) (768px and down) */
@media only screen and (max-width: 768px) {
  #nav-logo {
    width: 2.5rem;
  }
  #nav-name {
    font-size: 1.5rem;
  }
}

/* Medium and large devices (768px and up) */
@media only screen and (min-width: 768px) {
  #nav-logo {
    width: 3.5rem;
  }
  #nav-name {
    font-size: 2rem;
  }
}

.v-toolbar__title {
  overflow: visible;
}

.v-app-bar a {
  text-decoration: none !important;
}
</style>